import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ListS2ApplicationComponent } from './list-s2-application.component';

const routes: Routes = [
  {
    path: '',
    component: ListS2ApplicationComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ListS2ApplicationRoutingModule { }
